  }

  #dashboard header .menu-btn .fa-bell {
    margin-right: 5px;
    font-size: 1rem;
  }

  #dashboard .welcome-section .img {
    margin-left: 10px;
  }

  #dashboard .welcome-section .content {
    margin-left: 7px;
  }

  #dashboard .welcome-section .content h2 {
    font-size: 1.2rem;
  }

  #dashboard .welcome-section .content p {
    font-size: 0.7rem;
  }

  #dashboard .content-section .content-box {
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr 1fr;
    grid-gap: 25px;
  }
}

#console {
  height: 100vh;
}

#console header {
  width: 100vw;
  height: 8vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#console header .logo {
  width: calc(100vw - 85vw);
  min-width: 150px;
  border-right: 2px solid #fbfbfc;
  padding: 8px 10px;
}

#console header .logo .logo-img {
  height: 40px;
  width: 40px;
  background: url(/Project-Odyssey/static/media/consoleLogo.1990a2ad.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  cursor: pointer;
}

#console header .search-bar {
  padding: 7px 15px 7px 15px;
  width: calc(100vw - 30vw);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-right: 2px solid #fbfbfc;
}

#console header .search-bar .fa-search {
  color: #273f54;
  padding-right: 15px;
  font-size: 1.1rem;
}

#console header .search-bar .search-input {
  padding: 10px 15px 10px 15px;
  width: 60%;
  border: none;
  font-size: 1.2rem;
  font-weight: 500;
  outline: none;
  color: #273f54;
}

#console header .menu-btn {
  width: calc(100vw - 85vw);
  padding: 8px 35px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#console header .menu-btn i {
  padding: 10px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.1rem;
  color: #c8ced3;
}

#console header .menu-btn i:hover {
  background: #f2f3f5;
}

#console .content-section {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  width: 100vw;
}

#console .content-section .select-menu {
  width: calc(100vw - 85vw);
  max-width: calc(100vw - 85vw);
  height: calc(100vh - 8vh);
  background: #fbfbfc;
  padding: 15px;
}

#console .content-section .select-menu ul {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 15px 5px;
  position: relative;
  cursor: pointer;
}

#console .content-section .select-menu ul:hover {
  background: #f2f3f5;
}

#console .content-section .select-menu ul i {
  color: #273f54;
  padding-right: 10px;
  cursor: pointer;
}

#console .content-section .select-menu ul .fa-angle-down {
  padding-left: 20px;
  cursor: pointer;
  position: absolute;
  top: 10;
  right: 0;
}

#console .content-section .select-menu ul .fa-angle-up {
  padding-left: 20px;
  cursor: pointer;
  position: absolute;
  top: 10;
  right: 0;
}

#console .content-section .select-menu ul a {
  text-decoration: none;
  color: black;
}

#console .content-section .select-menu .sub-list {
  color: #66c9a8;
  font-weight: 600;
  padding-left: 15px;
}

#console .content-section .select-menu h3 {
  font-size: 1.1rem;
  font-weight: 700;
  padding-top: 10px;
  padding-bottom: 5px;
}

#console .content-section .select-menu ul span {
}

#console .content-section .content-area {
  width: calc(100vw - 15vw);
  max-width: calc(100vw - 15vw);
  height: calc(100vh - 8vh);
  background: #fbfbfc;
  padding: 15px;
}

#console .content-section .content-area .box {
  border-radius: 5px;
  box-shadow: 5px 5px 5px 0px rgba(187, 188, 192, 0.8);
}

#console .content-section .content-area .box .title-area {
