#task-container{
    padding: 10px;
    width: 100%;
    height: 100%;
}

#task-container .task-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#task-container .task-header .task-title{
    font-size: 1.5rem;
    font-weight: 700;
}

#task-container .task-header .create-task{
    background: #23CAC8;
    color: white;
    padding: 10px 20px;
    border-radius: 2px;
    box-shadow: 5px 5px 5px 0px rgba(187, 188, 192, 0.8);
    cursor: pointer;
    transition: all .2s ease-in-out;
}

#task-container .task-header .create-task span{
    font-size: 1.2;
    font-weight: 700;
}

#task-container .task-header .create-task:hover{
    background: #ff5253;
}

#task-container .task-box{
    padding-top: 20px;
    /*background: blueviolet;*/
    padding: 10px;
    
}

#task-container .task-box .task-box-header{
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    color: #b0b1b3;
    font-size: .9rem;
    font-weight: 700;
    padding-bottom: 10px;
}

#task-container .task-box .task-box-header .desc{}

#task-container .task-box .task-box-header .assigned{}

#task-container .task-box .task-box-header .priority{}

#task-container .task-box .task-data{
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
    grid-template-rows: repeat(1fr);
    background: white;
    padding: 15px 0;
    border-bottom: 3px solid #fbfbfc;
}

#task-container .task-box .task-data .desc-data{
    padding-left: 5px;
    font-weight: 600;
}

#task-container .task-box .task-data .assigned-data{
    color: #989898;
}

#task-container .task-box .task-data .priority-data{
    color: #989898;
}

#task-container .task-box .task-data .logo-btn{
    color: #989898;
    grid-column: 5;
    display: flex;
}

#task-container .task-box .task-data i{
    padding-right: 5px;
    cursor: pointer;
}

#task-container .task-box .new-task{
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    background: white;
    padding: 15px 0;
    border-bottom: 3px solid #fbfbfc;
    color: #23CAC8;
    padding-left: 5px;
    font-weight: 600;
    cursor: pointer;
}

#task-container .task-box .new-task .text{}


/* Phones */
@media only screen and (max-width: 767px) {   

    #task-container .task-box .task-box-header{
        display: grid;
        grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr;
